module.exports = "\n.panelTop {\n    display: block;\n    width: 100%;\n    height: 480px;\n}\n\n.panelTop .chart-list, .device-list, .tag-list {\n    height: 480px;\n}\n\n.panelBottom {\n    display: block;\n    width: 100%;\n    height: 160px;\n}\n\n.list {\n    width: 100% !important; \n    height: 100% !important; \n    font-size: 16px !important; \n    padding-top: 0px !important;\n}\n\n.list > span  {\n    padding-left: 10px;\n}\n\n.list mat-list-option  {\n    padding-left: 10px;\n}\n\n.chart-list {\n    overflow-y: auto;\n}\n\n.list-item {\n    display: block;\n    font-size: 14px;\n    height: 26px !important;\n    cursor: pointer;\n    overflow: hidden;\n    padding-left: 10px;\n}\n\n.list-item mat-icon {\n    font-size: 20px;\n}\n\n.list-item span {\n    width: calc(100% - 26px);\n    text-overflow: ellipsis;\n    overflow: hidden;\n}\n\n.list-item-text {\n    white-space: nowrap;\n    text-overflow: ellipsis;\n    overflow: hidden;\n}\n\n.list-item-hover:hover {\n    background-color: rgba(0, 0, 0, 0.1);\n}\n\n.list-item-selected {\n  background-color: rgba(0, 0, 0, 0.1);\n}\n\n.list-header {\n    background-color: rgba(0, 0, 0, 0.8);\n    color: #ffffff;\n    line-height: 26px;\n}\n\n.device-list {\n    margin-right: 2px;\n    overflow-y: auto;\n}\n\n.tag-list {\n    margin-right: 2px;\n    overflow-y: auto;\n}\n\n.color-line {\n    width: 30px;\n    border: unset;\n    border: 1px solid rgba(0,0,0,0.1);\n    border-radius: 2px;\n    opacity: 1;\n    background-color: #f1f3f4;\n}\n"